import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CRow,
  CInvalidFeedback,
  CFormGroup,
} from '@coreui/react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { signIn } from '../../../store/auth/actions';
import { useTranslation } from 'react-i18next';
import { RouteComponentProps, useHistory } from 'react-router-dom';
import { setUserLS, validate } from '../../../utils';
import { SUCCESS } from '../../../store/callApi/actions';
import { ls } from '../../../extensions';
import config from '../../../config';
import { RenderInputPassword } from '../containers/extensions';
import { useApi } from 'store/callApi';

const validationSchema = function (values: any) {
  return Yup.object().shape({
    email: Yup.string()
      .required('email-must-be-at-least-3-chara')
      .min(3, 'email-must-be-at-least-3-chara')
      .matches(config.validate.validateEmail(), 'email-format-is-incorrect'),
    // .matches(
    //   /(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])@(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])/,
    //   'Invalid email'
    // ),
    password: Yup.string()
      .required('Your password must be at least 8 characters long with one letter, and a number')
      .min(8, 'Your password must be at least 8 characters long with one letter, and a number')
      .matches(
        config.validate.validatePassword(),
        'Your password must be at least 8 characters long with one letter, and a number',
      ),
  });
};

const initialValues = {
  email: '',
  password: '',
};

const SignIn: React.FC<RouteComponentProps> = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const dispatch = useDispatch();
  const { callApi } = useApi();

  const [passwordShown, setPasswordShown] = useState(false);

  const onSubmit = (values: { email: string; password: string }) => {
    callApi(
      {
        method: 'post',
        api: config.rest.signInAdmin(),
        body: values,
        loading: true,
        msg: config.messages.signInFailure,
      },
      (response) => {
        const { status, data } = response;
        if (status === SUCCESS) {
          const dataExtra = { ...data, email: values.email };
          dispatch(signIn(dataExtra));
          if (data.challengename) {
            history.push('/admin/reset-password');
            ls.set('email', values.email);
            ls.set('session', data.session);
          } else {
            setUserLS(dataExtra);
            history.push(config.routes.managerUsers());
          }
        }
      },
    );
  };

  return (
    <div className="hb-admin c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <div className="justify-content-center">
          <CCardGroup>
            <CCard className="p-4" style={{ maxWidth: 560, margin: '0 auto' }}>
              <CCardBody>
                <Formik initialValues={initialValues} validate={validate(validationSchema)} onSubmit={onSubmit}>
                  {({ values, errors, touched, dirty, handleChange, handleBlur, handleSubmit, isValid }) => (
                    <CForm onSubmit={handleSubmit}>
                      <h1>{t('sign-in-admin')}</h1>
                      <p className="text-muted">{t('sign-in-to-your-account')}</p>
                      <CFormGroup>
                        <CInput
                          type="text"
                          placeholder="Email"
                          autoComplete="email"
                          invalid={values.email !== '' && touched.email && !!errors.email}
                          name="email"
                          maxLength={50}
                          value={values.email}
                          onBlur={handleBlur}
                          onChange={(e: any) => {
                            if (!e.target.value.includes(' ')) handleChange(e);
                          }}
                        />
                        <CInvalidFeedback>{t(errors.email || '')}</CInvalidFeedback>
                      </CFormGroup>
                      <RenderInputPassword
                        name="password"
                        label="Password"
                        autoComplete="current-password"
                        show={passwordShown}
                        setShow={() => setPasswordShown(!passwordShown)}
                        values={values}
                        errors={errors}
                        handleBlur={handleBlur}
                        handleChange={handleChange}
                        touched={touched}
                        hideLabel
                      />
                      <CFormGroup>
                        <CRow>
                          <CCol xs="6">
                            <CButton color="primary" className="px-4" type="submit" disabled={!(isValid && dirty)}>
                              {t('sign-in')}
                            </CButton>
                          </CCol>
                        </CRow>
                      </CFormGroup>
                    </CForm>
                  )}
                </Formik>
              </CCardBody>
            </CCard>
          </CCardGroup>
        </div>
      </CContainer>
    </div>
  );
};

export default SignIn;
